*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

:root {
  --bg-white-header: #fff;
  --bg-dark-theme-header: #111;
  --bg-dark-theme-body: #202024;
  --hover-focus: #4f2ee8;
  --hover-book: #eac645;
  --hover-text-categori-dark-theme: #eac645;
  --bg-current-page: #eac645;
  --bg-pagination-btn: #eac645;
  --bg-modal-book-white-theme: #fff;
  --bg-shoppinglist-page: #f6f6f6;
  --bg-home-page: #f6f6f6;
  --bg-home-page-cards-book: #f6f6f6;
  --bg-support-ukraine: radial-gradient( 183.6% 183.01% at 59.76% -23.42%, #4f2ee8 18.03%, #fff 100% );
  --box-shadow-bg-support: 0px 10px 10px rgba(245, 245, 247, .1);
  --bg-card-book-shoppinglist-page: #f6f6f6;
  --bg-card-book-shoppinglist-page-dark-theme: #202024;
  --bg-border-card-book-shoppinglist-page: rgba(79, 46, 232, .4);
  --text-color-card-book-shoppinglist-page: #111;
  --text-color-card-book-shoppinglist-page-dark-theme: #fff;
  --text-color-title-white: #fff;
  --text-color-title-dark: #111;
  --text-color-title-violet: #4f2ee8;
  --text-color-author-category-white-theme: #b4afaf;
  --text-color-title-category-white-theme: #b4afaf;
  --text-color-allcategories-white-theme: rgba(17, 17, 17, .6);
  --text-color-book-title-dark-theme: #fff;
  --text-color-allcategories-dark-theme: rgba(255, 255, 255, .6);
  --text-color-title-category-dark-theme: #b4afaf;
  --text-color-author-category-dark-theme: #b4afaf;
  --text-empty-shoplist: rgba(0, 0, 0, .6);
  --text-singup-btn: #f3f3f3;
}

