.header-container {
  background: var(--bg-white-header);
  border: 1.5px solid var(--text-color-title-dark);
  color: var(--text-color-title-dark);
  z-index: 31;
  border-radius: 0 0 8px 8px;
  padding-top: 18px;
  padding-bottom: 18px;
