  align-items: center;
  display: flex;
}

.logo-link {
  cursor: pointer;
  gap: 8px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.logo-link:hover, .logo-link:focus {
  transform: scale(1.2);
}

.logo-icon {
  width: 24px;
  height: 24px;
}

.logo-name {
