/*-----------------------------------MOBILE------------------------*/

.bookshelf{
padding-top: 40px;
}

.bestsellers-title{
    color: var(--text-color-title-dark);
font-weight: 700;
 font-size: 32px;
 line-height: 1.18;
letter-spacing: 0.04;
padding-bottom: 20px;
}


.bestsellers-category-name{
margin-bottom: 18px;
    color: #B4AFAF;
    font-weight: 400;
    font-size: 14px;
    line-height: 1.29;
    letter-spacing: 0.03;
    text-transform: uppercase;
}

.categories-title-last-word {
    color: var(--text-color-title-violet);
}


.category-book-list{
display: flex;
flex-direction: column;
padding-top: 20px;
}

.category-book-list>li:not(:last-child){
    margin-bottom: 40px;
}

.book-item{
cursor: pointer;
text-decoration: none;
color: inherit;
border: none;
}

@keyframes pulsare {
    0% {
        box-shadow: 0 0 25px #5ddcff,
            0 0 50px #4e00c2;
        background-color: rgb(102, 251, 251);
    }
}

.book-img-container{
    background-image: url('../images/svg-from-maket/plug.svg');
        background-color: inherit;
        background-repeat: no-repeat;
        background-position: 123px 205px;
            background-size: 88px 80px;
    position: relative;
width:335px;
height: 485px;
border-radius: 8px;
overflow: hidden;
margin-bottom: 14px;
}

.book-shoppingcart{
    background-image: url('../images/svg-from-maket/shoppingcart.svg');
    z-index: 10;
        background-color: inherit;
        background-repeat: no-repeat;
        background-size:36px 40px;
        position: absolute;
        top: 8px;
        right: 8px;
        width: 45px;
        height: 45px;
}

.bookshelf-bool-link:hover .book-img-container,
.bookshelf-bool-link:focus .book-img-container {
    animation: pulsare 1s ease-in-out;
}

.book-img{
    display: block;
height: 100%;
 width:100%;
object-fit: cover;
transition: transform .35s ease-in-out;
}

 .bookshelf-bool-link:hover .book-img, .bookshelf-bool-link:focus .book-img {
    transform: scale(1.03);
}

.books-overlay{
position: absolute;
    transform: translateY(100%);
    transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
    bottom: 0;
    left: 0;
    height: 70px;
    width: 100%;
    background-color: var(--hover-book);
    display: flex;
    align-items: center;
    justify-content: center;
    pointer-events: none;
}

.bookshelf-bool-link:hover .books-overlay,
.bookshelf-bool-link:focus .books-overlay {
    transform: translateY(0%);
}

.books-overlay-text{
font-weight: 500;
font-size: 14px;
line-height: 1.29;
letter-spacing:0.04;
color: var(--text-color-title-dark);
text-transform: uppercase;
}

.book-title{
width: 335px;
white-space: nowrap;
overflow: hidden;
text-overflow: ellipsis;
font-weight: 700;
font-size: 16px;
line-height: 1.125;
letter-spacing: 0.04;
color: var(--text-color-title-dark);
margin-bottom: 4px;
}

.author-name{
width: 335px;
white-space: nowrap;
overflow: hidden;
text-overflow: ellipsis;
font-style: italic;
font-size: 12px;
line-height: 1.17;
letter-spacing: 0.04;
color:#B4AFAF;
}

.bestsellers-button-container{
display: flex;
justify-content: end;
    padding-bottom: 40px;
}

.bestsellers-button{
padding-top: 14px;
    padding-right: 28px;
    padding-bottom: 14px;
    padding-left: 28px;
    margin-top: 6px;
    display: flex;
    border-radius: 40px;
    border: 2px solid var(--text-color-title-violet);
    font-size: 14px;
    font-weight: 700;
    line-height: 1.29;
    letter-spacing: 0.01;
    color: var(--text-color-title-dark);
    text-transform: uppercase;
    transition: transform .35s ease-in-out;
}

.bestsellers-button:hover, .bestsellers-button:focus {
    animation: pulsare 1s ease-in-out;
    background-color:var(--hover-focus);
    color:var(--text-color-title-white);
}

@keyframes pulsare {
    0% {
        box-shadow: 0 0 25px #5ddcff,
            0 0 50px #4e00c2;
    }
}

