/*---------------------------------SPINNER---------------------*/

.spinner-hidden {
    position: absolute;
    white-space: nowrap;
    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    overflow: hidden;

    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
}

.spinner-more {
    position:fixed;
    top: 50%;
    left:46%;
    z-index: 20;
    pointer-events: none;
    width: 40px;
        height: 40px;
    border-radius: 50%;
    border: 5px solid;
    border-color: var(--bg-border-card-book-shoppinglist-page);
    border-top-color: var(--hover-book);
    animation: spin 1s ease-in-out infinite;
    margin: auto;
   
}

@keyframes spin {
    to {
        transform: rotate(360deg);
    }
}

@media screen and (min-width: 768px) {
    .spinner-more {
        border: 10px solid;
        border-color: var(--bg-border-card-book-shoppinglist-page);
        border-top-color: var(--hover-book);
        position: fixed;
        top: 40%;
        left: 46%;
        width: 60px;
        height: 60px;
    }
}

@media screen and (min-width: 1400px) {
    .spinner-more{
    top: 50%;
    left: 50%;
    }
}
